<template>
<nav v-if="isDetail == false">
    <div class="header_top">
      <h1>{{title}}</h1>
      <a href="" @click="alert('준비중입니다', $event)"><img src="../assets/img/search.png" alt=""></a>
    </div>
    <div class="header_bottom">
      <router-link to="/interior/" class="linkBtn" :class="{ active : linkToggle[0]}" @click="toggleBtn(0)">인기글</router-link>
      <router-link to="/interior/follow" class="linkBtn" :class="{ active : linkToggle[1]}" @click="toggleBtn(1)">팔로잉</router-link>
      <router-link to="/interior/gallery" class="linkBtn" :class="{ active : linkToggle[2]}" @click="toggleBtn(2)">갤러리</router-link>
      <router-link to="/interior/guide" class="linkBtn" :class="{ active : linkToggle[3]}" @click="toggleBtn(3)">포스트</router-link>
    </div>
  </nav>
  <router-view></router-view>
  <footer v-if="isDetail == false">
    <router-link to="/interior/" class="linkBtn" :class="{ active : linkToggle[0]}" @click="toggleBtn(0)"></router-link>
    <router-link to="/interior/follow" class="linkBtn" :class="{ active : linkToggle[1]}" @click="toggleBtn(1)"></router-link>
    <a href="" @click="alert('준비중입니다', $event)"></a>
    <router-link to="/interior/gallery" class="linkBtn" :class="{ active : linkToggle[2]}" @click="toggleBtn(2)"></router-link>
    <router-link to="/interior/guide" class="linkBtn" :class="{ active : linkToggle[3]}" @click="toggleBtn(3)"></router-link>
  </footer>
</template>
<script>

import "swiper/swiper.scss";
import { mapMutations, mapState } from "vuex";

export default {
  name: "index",
  computed: {
    ...mapState(["data","isDetail"]),
  },
  methods:{
    ...mapMutations(["detail", "resetDetail"]),
    toggleBtn(i){
      this.linkToggle.fill(false);
      this.linkToggle[i] = true;
      this.title = this.titleArray[i];
    },
    alert(a, e) {
      e.preventDefault();
      alert(a);
    },
  },
  
  mounted(){
    this.resetDetail();
    console.log(this.isDetail)
  },
  data() {
    return {
      linkToggle : [true, false, false, false],
      titleArray:["Home", "Following", "Gallery", "Post"],
      title:"Home"
    };
  },
};
</script>
