<template>
  <section id="post">
    <h2 class="sub_title">테마별 셀프가이드</h2>
    <div class="tag">
      <button @click="changeTag(0)" :class="{ active: isTag[0] }">전체</button>
      <button @click="changeTag(1)" :class="{ active: isTag[1] }">
        시공정보
      </button>
      <button @click="changeTag(2)" :class="{ active: isTag[2] }">수납</button>
      <button @click="changeTag(3)" :class="{ active: isTag[3] }">
        꾸미기팁
      </button>
    </div>
    <div v-for="(a, i) in data" :key="i">
      <div v-if="data[i].tag[tagNum] == true"><Post :data="data[i]" /></div>
    </div>
  </section>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Post from "./Post.vue";
export default {
  name: "guide",
  computed: {
    ...mapState(["data", "isDetail"]),
  },
  components: {
    Post,
  },
  methods: {
    ...mapMutations(["detail", "resetDetail"]),
    changeTag(i) {
      this.isTag = [false, false, false, false, false];
      this.tagNum = i;
      if (this.isTag[i] == true) {
        this.isTag[i] = false;
      } else {
        this.isTag[i] = true;
      }
    },
  },
  mounted() {
    this.resetDetail();
  },
  data() {
    return {
      tagNum: 0,
      isTag: [true, false, false, false, false],
    };
  },
};
</script>

