<template>
  <section></section>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
    name:"write",
    computed:{
        ...mapState(["isDetail"]),
    },
    methods: {
      ...mapMutations(["detail"]),
      alert(a,e){
            e.preventDefault();
            alert(a);
        }
  },
  mounted(){
    this.detail();
  },
}
</script>

