<template>
  <div class="detailBg" :style="{ backgroundImage: `url(${eventBg[isBlack].bg})` }">
    <section class="conBox">
      <div class="title">
        <p class="contTitle">임스 플라이우드 라운지 체어</p>
        <p class="price">
          <span>1,860,000원</span> 1,840,000원
        </p>
        <div class="color">
          <button @click="isBlack = 2" :class="{ active: isBlack == 2 }"></button>
          <button @click="isBlack = 0" :class="{ active: isBlack == 0 }"></button>
          <button @click="isBlack = 1" :class="{ active: isBlack == 1 }"></button>
        </div>
      </div>
      <div class="content">
        <img src="../assets/img/detail.png" alt="">
      </div>
    </section>
  </div>
  <div class="eventFoot">
    <a href="" @click="alert('준비중입니다.', $event)">바로구매</a>
    <a href="" @click="alert('준비중입니다.', $event)"><img src="../assets/img/star.png" alt=""></a>
    <a href="" @click="alert('준비중입니다.', $event)"><img src="../assets/img/cart2.png" alt=""></a>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
    name:"event",
    computed:{
        ...mapState(["isDetail"]),
    },
  methods: {
      ...mapMutations(["detail"]),
      alert(a,e){
            e.preventDefault();
            alert(a);
        }
  },
  mounted(){
    this.detail();
  },
  data(){
    return{
      eventBg:[
        {
          bg: require("../assets/img/chair1.png"),
        },
        {
          bg: require("../assets/img/chair2.png"),
        },
        {
          bg: require("../assets/img/chair3.png"),
        }
      ],
      isBlack : 2,
    }
  }
}
</script>

